import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmhangService } from './../../../services/dmhang.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { Router } from '@angular/router';
@Component({
  selector: 'app-dmhang-add',
  templateUrl: './dmhang-add.component.html',
  styleUrls: ['./dmhang-add.component.css']
})
export class DmhangAddComponent implements OnInit, OnDestroy {

	public subscription : Subscription;
	//public db : dmhang[] = [];
 	public db : dmhang;
  constructor(
  	public dmhangservice : DmhangService,
  	public routerService : Router

  	) { }

  ngOnInit() {
  	this.db = new dmhang();
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
  onAdd_dmhang(){
   	this.subscription = this.dmhangservice.add_dmhang(this.db).subscribe(data =>{
  		//console.log(data);
  		if(data)
  		{
  			this.routerService.navigate(['dmhang']);
  		}
  	});
  }

}
