import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmkhService } from './../../../services/dmkh.service';
import { Subscription } from 'rxjs';
import { dmkh } from './../../../models/dmkh.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dmkh-add',
  templateUrl: './dmkh-add.component.html',
  styleUrls: ['./dmkh-add.component.css']
})
export class DmkhAddComponent implements OnInit, OnDestroy {

	public subscription : Subscription;
	
 	public db : dmkh;
  constructor(
  		public dmkhservice : DmkhService,
  		public routerService : Router
  	) { }

  ngOnInit() {
  	this.db = new dmkh();
  }

  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
  onAdd_dmkh(){
   	this.subscription = this.dmkhservice.add_dmkh(this.db).subscribe(data =>{
  		//console.log(data);
  		if(data)
  		{
  			this.routerService.navigate(['dmkh']);
  		}
  	});
  }

}
